* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Encode Sans Semi Condensed;
}

body {
  background-color: #8a2be2;
}

.header {
  color: #fff;
  background-color: #002ca3;
  flex-direction: row;
  justify-content: space-between;
  width: 100vw;
  max-height: 130px;
  padding: 22px 40px;
  display: flex;

  & .logo-header {
    max-width: 76px;
  }

  & .header-content {
    flex-direction: column;
    justify-content: center;
    align-items: end;
    display: flex;
  }

  & .nav-content {
    display: none;
  }

  & a {
    align-self: center;
    font-size: 3vw;
    font-weight: 400;
  }

  & .bugermenu {
    align-items: center;
    width: 8vw;
    display: inline;
  }

  & .login {
    border-radius: 67px;
    padding: 2px 25px 4px;
    font-size: 16px;
    font-weight: 300;
  }
}

.Banner-Title {
  text-align: end;
  background-image: url("../img/banner.fe44045f.png");
  background-repeat: no-repeat;
  background-size: 100vw;
  justify-content: flex-end;
  width: 100vw;
  max-height: 842px;
  padding: 0 20px;

  & h1 {
    color: #fff;
    text-shadow: 1px 0 #002ca3, -1px 0 #002ca3, 0 1px #002ca3, 0 -1px #002ca3;
    padding-top: clamp(90px, 28vw, 500px);
    font-family: Fugaz One;
    font-size: 10vw;
  }
}

h2 {
  font-family: Fugaz One;
  font-size: 3.6vw;
  font-weight: 100;
}

h3 {
  font-family: Fugaz One;
  font-size: 7vw;
  font-weight: 100;
}

a {
  color: inherit;
  font-size: 2.5vw;
  font-weight: 700;
  text-decoration: none;
}

p {
  width: 65vw;
  font-size: 1.5vw;
}

.News-section {
  color: #002ca3;
  background-color: #fff;
  background-image: url("../img/background-1.6d102d8b.png");
  background-position: 0 51vw;
  background-repeat: no-repeat;
  background-size: 100vw;
  flex-direction: row;
  align-items: center;
  gap: 8vw;
  padding: 5vw 7vw 15vw;
  display: flex;

  & .album-cover {
    width: 40vw;
  }

  & .texts {
    flex-direction: column;
    gap: 1vw;
    width: 35vw;
    display: flex;
  }

  & .album-info {
    flex-direction: column;
    gap: 1vw;
    display: flex;
  }

  & .album-artist {
    font-size: 1.7vw;
  }

  & .album-title {
    font-size: 2.3vw;
    font-weight: 600;
  }

  & .link {
    padding-top: 1vw;
  }

  & .chevron-right {
    width: 1vw;
  }
}

.Playlist-section {
  text-align: end;
  color: #fff;
  background-color: #fff;
  background-image: url("../img/background-1.6d102d8b.png");
  background-position: 0 -9vw;
  background-repeat: no-repeat;
  background-size: 100vw;
  flex-direction: column;
  gap: 4vw;
  padding: 0 2vw 15vw;
  display: flex;

  & .playlists {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    display: flex;
  }

  & .playlist {
    width: 31vw;
  }

  & .playlist-small {
    width: 23vw;
  }

  & .next {
    border-radius: 67px;
    height: 20vw;
    padding: 2vw 2.5vw;
    display: block;

    & .chevron-white {
      width: 1.2vw;
      padding-bottom: 2px;
    }

    & .chevron-blue {
      display: none;
    }
  }

  & .next:hover {
    & .chevron-white {
      display: none;
    }

    & .chevron-blue {
      width: 1.2vw;
      display: block;
    }
  }
}

.artist-image {
  border: 1.2vw solid #000;
  border-radius: 100%;
  width: 25vw;
  box-shadow: 0 0 0 .2vw #002ca3, 0 0 0 .7vw #000, 0 0 0 1vw #0043fc, 0 0 0 2.3vw #000;
}

.Artist-section {
  color: #002ca3;
  text-align: end;
  background-color: #fff;
  background-image: url("../img/background-2.137290f5.png");
  background-position: 0 97vw;
  background-repeat: no-repeat;
  background-size: 100vw;
  flex-direction: column;
  gap: 3vw;
  padding: 0 5vw 12vw;
  display: flex;

  & .artist-cards {
    flex-direction: column;
    gap: 2vw;
    display: flex;
  }

  & .artist-card {
    flex-direction: row;
    align-items: center;
    gap: 7vw;
    display: flex;
  }

  & .TracyNelson {
    padding-left: 10vw;
  }

  & .artist-info {
    flex-direction: column;
    display: flex;
  }
}

.Shop-section {
  color: #fff;
  background-image: url("../img/background-2.137290f5.png");
  background-position: 0 -9vw;
  flex-direction: column;
  gap: 2vw;
  padding: 3vw 7vw 2vw;
  display: flex;

  & .chevron-right {
    width: 1vw;
  }

  & .carousel {
    flex-direction: column;
    display: flex;
  }

  & .shop-albums {
    flex-direction: row;
    height: 31vw;
    display: flex;
    position: relative;
  }

  & .album-cover {
    width: 31vw;
    position: absolute;
    top: 0;
    left: 50%;
  }

  & .album-cover.front {
    z-index: 1;
  }

  & .album-cover.prev {
    left: 40%;
  }

  & .album-cover.next {
    left: 60%;
  }

  & .shop-indicators {
    flex-direction: row;
    gap: .4vw;
    padding-top: 2.5vw;
    padding-left: 54vw;
    display: flex;
  }

  & .indicator {
    background-color: #000;
    border-width: .3vw;
    border-color: #002ca3;
    border-radius: 100%;
    width: 3vw;
    height: 3vw;
    display: flex;
  }

  & .clicked {
    background-color: #002ca3;
    border-color: #000;
  }
}

.About-section {
  color: #002ca3;
  background-color: #fff;
  background-image: url("../img/background-2.137290f5.png");
  background-position: 0 -47vw;
  background-repeat: no-repeat;
  background-size: 100vw;
  flex-direction: column;
  gap: 5vw;
  padding: 7vw 3vw 3vw;
  display: flex;

  & .about-texts {
    flex-direction: column;
    gap: 3vw;
    display: flex;
  }

  & .loogos-labels {
    gap: 1vw;
    display: flex;
  }

  & .logos {
    align-items: center;
    width: 15vw;
    height: min-content;
  }
}

.footer {
  color: #fff;
  background-color: #002ca3;
  flex-direction: column;
  gap: 40px;
  width: 100vw;
  padding: 40px 40px 20px;
  display: flex;

  & p {
    align-self: center;
  }

  & .footer-main {
    flex-direction: row;
    justify-content: space-between;
    display: flex;
  }

  & .Information {
    flex-direction: row;
    align-items: center;
    gap: 20px;
    display: flex;
  }

  & .CraftsRec-logo {
    width: clamp(40px, 16vw, 80px);
  }

  & .Legal-Informations {
    text-align: center;
    font-size: clamp(8px, 2.5vw, 40px);
    line-height: clamp(12px, 4vw, 60px);
    list-style: none;
  }

  & .policies:hover {
    text-decoration: underline 3px;
  }

  & .Contact {
    flex-direction: row;
    align-items: center;
    gap: 10px;
    display: flex;
  }

  & .Social-Media {
    flex-direction: column;
    gap: 10px;
    display: flex;
  }

  & .media-logos {
    max-width: 6vw;
  }

  & .footer-bluesville-logo {
    max-width: 100px;
  }
}

button {
  color: #fff;
  background-color: #002ca3;
  border: 1px solid #fff;
  display: none;
}

button:hover {
  color: #002ca3;
  background-color: #fff;
}

.Biography-section {
  color: #002ca3;
  background-color: #fff;
  background-image: url("../img/background-3.ed13a8ec.png");
  background-position: 0 45vw;
  background-repeat: no-repeat;
  background-size: 100vw;
  flex-direction: row;
  justify-content: center;
  align-items: end;
  gap: 2vw;
  padding: 9vw 8vw 16vw 4vw;
  display: flex;

  & h3 {
    font-size: 8vw;
  }

  & p {
    color: #002ca3;
    width: 31vw;
  }
}

.Discography-section {
  text-align: end;
  color: #fff;
  background-image: url("../img/background-3.ed13a8ec.png");
  background-position: 0 -17vw;
  background-repeat: no-repeat;
  background-size: 100vw;
  flex-direction: column;
  gap: 5vw;
  width: 100vw;
  padding: 4vw 4vw 0;
  display: flex;

  & .albums-grid {
    width: 28vw;
  }

  & .link {
    color: #fff;
    align-self: center;
    font-weight: 600;
  }

  & .grid {
    text-align: center;
    grid-template: "\31 " 1fr
                   "\32 " 1fr
                   "\33 " 1fr
                   "\34 "
                   "\35 "
                   "\36 "
                   "\37 "
                   "\38 "
                   "link"
                   / 1fr 1fr 1fr;
    row-gap: 2vw;
    display: grid;
  }
}

@media screen and (width >= 768px) {
  .header {
    & .bugermenu {
      width: 6vw;
    }
  }

  .Banner-Title {
    padding: 0 30px;

    & h1 {
      padding-top: clamp(240px, 30vw, 700px);
      padding-left: 100px;
      font-size: clamp(10px, 9vw, 80px);
    }
  }

  .Biography-section {
    background-position: 0 42vw;
    gap: 10vw;
    padding: 9vw 7vw 17vw 4vw;

    & h3 {
      font-size: 7vw;
    }

    & p {
      width: 36vw;
    }
  }

  .footer {
    gap: 20px;
    padding: 30px 40px 20px;

    & .Legal-Informations {
      columns: 2;
      text-align: start;
      column-gap: 0;
      font-size: clamp(10px, 2.5vw, 40px);
      line-height: clamp(12px, 4vw, 60px);
    }

    & .media-logos {
      max-width: 5vw;
    }

    & .footer-bluesville-logo {
      width: 13vw;
      max-width: 138px;
    }
  }
}

@media screen and (width >= 1024px) {
  .header {
    max-height: 130px;
    padding: 22px 40px;

    & .logo-header {
      max-width: 76px;
    }

    & .nav-content {
      flex-direction: row;
      gap: 35px;
      padding-right: 140px;
      font-size: clamp(20px, 3vw, 45px);
      list-style: none;
      display: flex;
    }

    & .bugermenu {
      display: none;
    }

    & button {
      display: block;
    }

    & .login {
      font-size: 1vw;
    }
  }

  .Banner-Title {
    padding: 0 40px;

    & h1 {
      padding-top: clamp(50px, 47vw, 700px);
      padding-left: 40px;
      font-size: 80px;
    }
  }

  .Biography-section {
    background-position: 0 40vw;
    gap: 20vw;
    padding: 9vw 7vw 17vw 4vw;

    & h3 {
      font-size: 6vw;
    }

    & p {
      width: 41vw;
    }
  }

  .Discography-section {
    & .grid {
      grid-template: "albums-grid1" 1fr
                     "albums-grid2" 1fr
                     "albums-grid3" 1fr
                     "albums-grid4"
                     "albums-grid5"
                     "albums-grid6"
                     "albums-grid7"
                     "albums-grid8"
                     "link"
                     / 1fr 1fr 1fr;
    }
  }

  .footer {
    padding: 20px 40px;

    & .footer-main {
      flex-direction: row;
      justify-content: space-between;
    }

    & .Information {
      flex-direction: row;
      align-items: center;
      gap: 5vw;
      margin-right: 10vw;
    }

    & .CraftsRec-logo {
      width: 8vw;
      max-width: 129px;
    }

    & .Contact {
      flex-direction: row;
      align-items: center;
    }

    & .Social-Media {
      flex-direction: column;
      gap: 10px;
    }

    & .media-logos {
      width: 4vw;
      max-width: 55px;
    }

    & .footer-bluesville-logo {
      width: 10vw;
      max-width: 186px;
    }
  }
}
